.my-favorites-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;

  h1 {
    font-size: 24px;
    margin-bottom: 30px;
    text-align: center;
  }

  .empty-message {
    text-align: center;
    padding: 50px 0;

    p {
      font-size: 18px;
      margin-bottom: 20px;
    }

    .go-to-main {
      display: inline-block;
      padding: 10px 20px;
      background-color: #ff6b6b;
      color: white;
      border-radius: 5px;
      text-decoration: none;
    }
  }

  .favorites-list {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
  width: 500px;
  margin: 0 auto;
    .favorite-item {
      border: 1px solid #e0e0e0;
      border-radius: 8px;
      overflow: hidden;
      position: relative;
      transition: transform 0.3s;

      &:hover {
        transform: translateY(-5px);
      }

      .store-link {
        display: flex;
        text-decoration: none;
        color: inherit;
      }

      .store-image {
        width: 120px;
        height: 120px;
        flex-shrink: 0;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        .no-image {
          width: 100%;
          height: 100%;
          background: #f5f5f5;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #999;
        }
      }

      .store-info {
        padding: 15px;
        flex-grow: 1;

        h3 {
          padding: 10px 0px;
          margin: 0 0 5px 0;
          font-size: 18px;
        }

        .category {
          color: #666;
          margin: 0 0 5px 0;
          font-size: 14px;
        }

        .reviews {
          color: #666;
          margin: 0;
          font-size: 14px;
        }
      }

      .remove-btn {
        position: absolute;
        top: 10px;
        right: 10px;
        background: rgba(255, 0, 0, 0.7);
        color: white;
        border: none;
        padding: 5px 10px;
        border-radius: 5px;
        cursor: pointer;
        font-size: 12px;

        &:hover {
          background: rgba(255, 0, 0, 0.9);
        }
      }
    }
  }

  .loading {
    text-align: center;
    padding: 50px;
    font-size: 18px;
  }
}


/* ===============================
   📱 My Favorites 반응형 최적화
   (원래 코드 밑에 그대로 추가)
=============================== */

/* 1024px 이하: 리스트 폭 약간 줄이기 */
@media (max-width: 1024px) {
  .my-favorites-container {
    padding: 16px;

    .favorites-list {
      width: 100%;
      max-width: 700px;
      margin: 0 auto;
    }
  }
}

/* 768px 이하: 카드 하나씩, 폭 넓게 */
@media (max-width: 768px) {
  .my-favorites-container {
    padding: 16px 12px;

    h1 {
      font-size: 22px;
      margin-bottom: 24px;
    }

    .favorites-list {
      width: 100%;
      max-width: 100%;
      grid-template-columns: 1fr;
      gap: 16px;
    }

    .favorite-item {
      .store-link {
        flex-direction: row; /* 아직은 가로 배치 유지 */
      }

      .store-image {
        width: 110px;
        height: 110px;
      }

      .store-info {
        padding: 12px;

        h3 {
          font-size: 16px;
        }

        .category,
        .reviews {
          font-size: 13px;
        }
      }
    }
  }
}

